
<template>
  <div class="mokuai">
    <div class="mk-box">
      <h5>新增Article</h5>
    </div>
    <el-form ref="form" :model="addForm" label-width="80px">
      <el-form-item label="标题名称">
        <el-input v-model="addForm.title"></el-input>
      </el-form-item>
      <el-form-item label="类    型">
        <el-select v-model="addForm.type" placeholder="全部">
          <el-option label="首页banner" value="0"></el-option>
          <el-option label="找职位banner" value="1"></el-option>
          <el-option label="找精英banner" value="2"></el-option>
          <el-option label="行业大图" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="说 明">
