<script>
import EditorBar from '../wangEnduit'

export default {
  components: { EditorBar },
  name: "mokuai",
  data() {
    return {
        isClear: false,
        detail:"",
      addForm: {
        title: "",
        type: "",
        content: "",
        url: "",
        dialogImageUrl: "",
        dialogVisible: false
      }
    };
  },
  methods: {
    change(val) {
      console.log(val)
      
    },  
    qqq(){
      const id="13717"
    this.axios({
            url:`/api/a/article/${id}`,
            method: "get",
          })
          .then(res => {
            console.log(res);
            
          });
    },
    addOnline() {
      if (
        this.addForm.title &&
        this.addForm.type &&
        this.addForm.dialogImageUrl &&
        this.addForm.url &&
        this.addForm.type
      ) {
        this.axios({
          url: "/api/a/u/article",
          method: "post",
          data:`title= ${this.addForm.title}&type=${this.addForm.type}&status=2&img=${this.addForm.dialogImageUrl}&url=${this.addForm.url}&content=${this.addForm.content}`,
          emulateJSON: true
        }).then(res => {
           alert("添加成功")
           this.$router.push('/home/search')
          console.log(res);
          
        });
      }else{
        alert("请输入完整的数据")
      }
    }, addDraft() {
      if (
        this.addForm.title &&
        this.addForm.type &&
        this.addForm.dialogImageUrl &&
        this.addForm.url &&
        this.addForm.type
      ) {
        this.axios({
          url: "/api/a/u/article",
          method: "post",
          data:`title= ${this.addForm.title}&type=${this.addForm.type}&status=1&img=${this.addForm.dialogImageUrl}&url=${this.addForm.url}&type=${this.addForm.type}&content=${this.addForm.content}`,
          emulateJSON: true
        }).then(res => {
           alert("添加成功")
           this.$router.push('/home/search')
        });
      }else{
        alert("请输入完整的数据")
      }
    },
    handlePictureCardPreview(file) {
      this.addForm.dialogVisible = true;
    },
    handlePictureCardSuccess(file) {
      console.log(file);
      this.addForm.dialogImageUrl = file.data.url;
      console.log(this.addForm.dialogImageUrl);
    },

    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制上传 1 张图片，已成功上传1张图片`);
    }
  }}
</script>


